/* ============== 01 global Style ============== */

@import url("https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Varela&display=swap");

/* Adicione isso ao seu arquivo de estilos */
@media print {
  * {
      -webkit-print-color-adjust: exact; /* Para Safari */
      color-adjust: exact; /* Navegadores mais antigos */
      print-color-adjust: exact; /* Padrão */
  }
}

html,
body {
  font-size: 16px;
  color: #424242;
  font-family: "Varela", sans-serif;
  vertical-align: baseline;
  font-weight: 400;
  overflow-x: hidden;
  line-height: 1.8;
  background: #fff;
  overflow-y: auto;
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Cor de fundo semi-transparente */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; /* Definir z-index alto para manter o popup na frente de outros elementos */
}

.transcription {
  text-align: left; /* Alinha o texto à esquerda */
}

.transcription strong {
  font-weight: bold; /* Define o estilo para negrito */
}

.action-result {
  text-align: left; /* Alinha o texto à esquerda */
}

.action-result strong {
  font-weight: bold; /* Define o estilo para negrito */
}

.popup-content {
  text-align: left;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  max-width: 80%;
  max-height: 80%;
  overflow: auto;
  position: relative; /* Garantir que os botões estejam posicionados relativamente ao conteúdo do popup */
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
}

.copy-button, .close-button {
  padding: 10px 20px;
  margin-top: 10px;
  border: none;
  background-color: #007bff; /* Cor de fundo do botão */
  color: #fff; /* Cor do texto do botão */
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.copy-button:hover, .close-button:hover {
  background-color: #0056b3; /* Mudança de cor ao passar o mouse */
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  cursor: pointer;
}

input,
textarea,
select {
  border: 0;
  background: transparent;
  transition: 0.4s;
}

input:focus,
textarea:focus,
select:focus {
