.primary-btn {
  color: #ffffff;
  position: relative;
  display: inline-block;
  overflow: hidden;
  padding: 15px 50px;
  border-radius: 10px;
  text-align: center;
  text-transform: capitalize;
  background: #405925;
}

@media screen and (max-width: 767px) {
  .primary-btn {
    padding: 10px 30px;
  }
}

.primary-btn span {
  color: #ffffff;
  position: relative;
  z-index: 2;
}

.primary-btn::before,
.primary-btn::after {
  position: absolute;
  top: 50%;
  content: "";
  width: 20px;
  height: 20px;
  background-color: #88A9A0;
  border-radius: 10%;
  color: #ffffff;
}

.primary-btn::before {
  left: -20px;
  transform: translate(-50%, -50%);
}

.primary-btn::after {
  right: -20px;
  transform: translate(50%, -50%);
}

.primary-btn:hover {
  color: #ffffff;
}

.primary-btn:hover::before {
  -webkit-animation: criss-cross-left 0.8s both;
  animation: criss-cross-left 0.8s both;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
}

.primary-btn:hover::after {
  -webkit-animation: criss-cross-right 0.8s both;
  animation: criss-cross-right 0.8s both;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
}

