/* ============== 03 header section ============== */

.sc-header-section,
.inner-header-style {
  position: absolute;
  width: 100%;
  z-index: 9;
  padding: 25px 0;
  transition: 0.6s;
  -webkit-transition: 0.6s;
  -moz-transition: 0.6s;
}

.sc-header-section .container-fluid,
.inner-header-style .container-fluid {
  padding-left: 80px;
